
import { reactive, ref, onBeforeUnmount } from "vue";
import { getAge } from "../../utils/getAge";
import m2 from "../../assets/img/m2.png";
import word from "../../assets/img/word.png";
export default {
  name: "author",
  setup(): unknown {
    let age: number = getAge("2000-10-18");
    let timer: number | null = null;
    let showActiveIndex = ref<number>(0);
    const p_text = ref<HTMLElement | string>("p_text");

    function setTimer(): void {
      timer = window.setInterval((): void => {
        showActiveIndex.value = showActiveIndex.value == 0 ? 1 : 0;
      }, 4000);
    }

    setTimer();

    onBeforeUnmount((): void => {
      // 销毁定时器
      clearInterval(timer as number);
    });

    // 创建图片列表
    const imageList = reactive([
      {
        path: word,
        index: 0,
      },
      {
        path: m2,
        index: 1,
      },
    ]);

    const move = function (e: MouseEvent) {
      if (!p_text.value) {
        return;
      }
      let baseX: number = Math.floor(window.innerWidth / 20);
      let baseY: number = Math.floor(window.innerHeight / 20);

      (p_text.value as HTMLElement).style.transform = `translate(${
        Math.floor(e.pageX / 10) - baseX
      }px,${Math.floor(e.pageY / 10) - baseY}px)`;
    };

    const leave = function () {
      if (!p_text.value) {
        return;
      }
      (p_text.value as HTMLElement).style.transform = "translate(0px,0px)";
    };

    return { age, imageList, showActiveIndex, move, p_text, leave };
  },
};
